@import url("https://fonts.googleapis.com/css2?family=Lato&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: "Lato", sans-serif;
  background-color: #f4f4f4;
}

.container {
  max-width: 1366px;
  width: 95%;
  margin: 0 auto;
}

.btn {
  padding: 0.7em 1.5em;
  border: 0;
  font-weight: bold;
  border-radius: 2px;
  cursor: pointer;
}

.btn__remove {
  background-color: #ff5f5f;
  color: white;
  transition: background-color 0.2s;
  font-size: 12px;
}

.btn__remove:hover {
  background-color: white;
  color: #ff5f5f;
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.2);
}

.btn__submit {
  background-color: #5fff74;
  color: black;
  transition: background-color 0.2s;
  font-size: 12px;
}

.btn__submit:hover {
  background-color: white;
  color: black;
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.2);
}

.kanban__board {
  padding: 3em 0;
}

.kanban__board h1 {
  text-align: center;
  font-size: 2.6em;
  text-transform: uppercase;
}

.board__grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-gap: 20px;
  margin: 2em 0;
}

.board__column {
  background-color: #fff;
  padding: 15px;
  border-radius: 5px;
}

.col__head {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.column__card {
  background-color: #f8f8f8;
  padding: 10px;
  margin: 0.5em 0;
  border-radius: 3px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.col__add {
  display: grid;
  place-items: center;
  font-size: 25px;
  color: #41d965;
  cursor: pointer;
}

.arrow__up {
  grid-area: up;
}
.arrow__left {
  grid-area: left-d;
}
.arrow__right {
  grid-area: right-d;
}
.arrow__down {
  grid-area: down;
}

.direction__btn__group {
  display: grid;
  grid-template-areas:
    "up up"
    "left-d right-d"
    "down down";
  justify-content: center;
  grid-gap: 10px;
  align-items: center;
}

.direction__btn__group i {
  cursor: pointer;
  font-size: 18px;
  text-align: center;
  color: #3f65ff;
}

.column__card__title {
  font-size: 18px;
  font-weight: normal;
  text-transform: capitalize;
  cursor: pointer;
}

.add__card__form {
  background-color: white;
  border-radius: 5px;
  width: 100%;
  margin-top: 10px;
}

.add__card__form input,
.add__card__form textarea {
  width: 100%;
  padding: 4px 8px;
  border: 1px solid #cecece;
  border-radius: 2px;
  display: block;
  margin-bottom: 10px;
  font-size: 14px;
  outline-width: 0;
}

::placeholder {
  font-size: 12px;
  font-family: "Lato", sans-serif;
}

.card__modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.089);
  display: flex;
  justify-content: center;
  align-items: center;
}

.card__modal__body {
  background-color: white;
  padding: 30px;
  border-radius: 5px;
  max-width: 650px;
  width: 100%;
}

.card__modal__body h2 {
  text-transform: uppercase;
}

.card__modal__body p {
  font-size: 18px;
  margin-top: 10px;
  color: #343434;
}

.card__modal__head {
  display: flex;
  justify-content: space-between;
  align-content: center;
}

.card__modal__head i {
  color: #ff5f5f;
  font-size: 25px;
  cursor: pointer;
  margin-left: 5px;
}

.form__control {
  width: 100%;
  padding: 0.8em;
  border: 1px solid #cecece;
  border-radius: 3px;
  display: block;
  margin-bottom: 10px;
  font-size: 16px;
  outline-width: 0;
}
